import { mapState,mapMutations } from 'vuex'
import PubClassify from '@/components/publish/pubClassify.vue'
import Relation from '@/components/publish/relationType.vue'
export default {
	data() {
		return {
			relation:'',
			relationType:'',
			classfiyType:'learn',
			imageList:[],
			text:'',
			title:''
		}
	},
	computed:{
		...mapState({
			msgType:state=>state.msgType,
			userInfo:state=>state.userInfo,
			sessionKey:state=>state.sessionKey
		})
	},
	components:{
		PubClassify,
		Relation
	},
	methods: {
		getClass(data){
			this.classfiyType=data.classType
		},
		getRelation(data){
			this.relationType=data.relationType;
			console.log(this.relationType)
		},
		getPicture(){
			let that=this;
			uni.chooseImage({
				count:1,
				sizeType:'compressed',
				success:(res)=>{
					that.imageList=res.tempFilePaths
				}
			})
		},
		getPreview(index){
			let that=this;
			uni.previewImage({
				current:index,
				urls:that.imageList
			})
		},
		deleteImg(index){
			this.imageList.splice(index,1)
		},
		foundSubmit(){
			let that=this;
			if(!that.sessionKey){
				uni,uni.showToast({
					title: '请先完成授权!',
					icon:'none'
				});
				return false;
			}
			if(that.title===''){
				return uni.showToast({
					title:'请填写标题',
					icon:'none'
				})
				return false;
			}
			if(that.imageList.length===0){
				return uni.showToast({
					title:'请选择一张图片',
					icon:'none'
				})
				return false;
			}
			if(that.classfiyType===''){
				return uni.showToast({
					title:'请选择类别',
					icon:'none'
				})
				return false;
			}
			if(that.relation===''||that.relateFoundType===''){
				return uni.showToast({
					title:'请填写联系方式',
					icon:'none'
				})
				return false;
			}
			if(that.relationType==='tel'){
				if(!(/^1[3456789]\d{9}/.test(that.relation))){
					uni.showToast({
						title:'手机号有误,请重填!',
						icon:'none'
					})
					return false;
				}
			}
			this.$api.pubGood(that.imageList[0],{
				sessionKey:that.sessionKey,
				goodType:that.msgType,
				goodTitle:that.title,
				goodTexts:that.text,
				goodClass:that.classfiyType,
				relation:that.relationType+':'+that.relation
			}).then(res=>{
				that.imgList=[]
				that.title='';
				that.text='';
				that.imageList=[];
				that.relation='';
				uni.showToast({
					title: JSON.parse(res).msg
				});
			})
		}
	}
}
